"use client";

import React, { useState } from 'react';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Tabs, TabsList, TabsTrigger, TabsContent } from './ui/tabs';

type AuthMode = 'login' | 'register' | 'license';

export default function AuthenticationForm() {
  const [activeMode, setActiveMode] = useState<AuthMode>('login');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [license, setLicense] = useState('');
  const [tfaCode, setTfaCode] = useState('');
  const [statusMsg, setStatusMsg] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setStatusMsg(null);
    try {
      let endpoint = '';
      let payload = {};

      if (activeMode === 'login') {
        endpoint = '/api/auth/login';
        payload = { username, password, tfaCode };
      } else if (activeMode === 'register') {
        endpoint = '/api/auth/register';
        payload = { username, password, license };
      } else if (activeMode === 'license') {
        endpoint = '/api/auth/license';
        payload = { license, tfaCode };
      }

      const response = await fetch(endpoint, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      });
      
      const data = await response.json();
      
      if (!response.ok) {
        throw new Error(data.message || 'Authentication failed');
      }

      // Store user info in localStorage for dashboard
      if (data.success && data.user && data.user.info) {
        localStorage.setItem('userInfo', JSON.stringify(data.user.info));
        // Redirect to dashboard
        window.location.href = '/dashboard';
      }
      
      setStatusMsg('Authentication successful!');
    } catch (error: any) {
      setStatusMsg(error.message);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4 w-80">
      <Tabs value={activeMode} onValueChange={(value) => setActiveMode(value as AuthMode)}>
        <TabsList>
          <TabsTrigger value="login">LOGIN</TabsTrigger>
          <TabsTrigger value="register">REGISTER</TabsTrigger>
          <TabsTrigger value="license">LICENSE</TabsTrigger>
        </TabsList>

        <TabsContent value="login">
          <div>
            <label className="block mb-1">Username</label>
            <Input value={username} onChange={(e) => setUsername(e.target.value)} placeholder="USERNAME" />
          </div>
          <div>
            <label className="block mb-1">Password</label>
            <Input 
              type="password" 
              value={password} 
              onChange={(e) => setPassword(e.target.value)} 
              placeholder="PASSWORD" 
            />
          </div>
          <div>
            <label className="block mb-1">2FA (Optional)</label>
            <Input value={tfaCode} onChange={(e) => setTfaCode(e.target.value)} placeholder="2FA CODE" />
          </div>
        </TabsContent>

        <TabsContent value="register">
          <div>
            <label className="block mb-1">Username</label>
            <Input value={username} onChange={(e) => setUsername(e.target.value)} placeholder="USERNAME" />
          </div>
          <div>
            <label className="block mb-1">Password</label>
            <Input 
              type="password" 
              value={password} 
              onChange={(e) => setPassword(e.target.value)} 
              placeholder="PASSWORD" 
            />
          </div>
          <div>
            <label className="block mb-1">License</label>
            <Input value={license} onChange={(e) => setLicense(e.target.value)} placeholder="LICENSE" />
          </div>
        </TabsContent>

        <TabsContent value="license">
          <div>
            <label className="block mb-1">License Key</label>
            <Input value={license} onChange={(e) => setLicense(e.target.value)} placeholder="LICENSE" />
          </div>
          <div>
            <label className="block mb-1">2FA (Optional)</label>
            <Input value={tfaCode} onChange={(e) => setTfaCode(e.target.value)} placeholder="2FA CODE" />
          </div>
        </TabsContent>
      </Tabs>

      {statusMsg && <p className="text-red-500">{statusMsg}</p>}
      <Button type="submit" className="w-full py-2 mt-4">
        {activeMode.toUpperCase()}
      </Button>
    </form>
  );
}
